import { Client, Databases, ID, Query } from 'appwrite';
import { Poll } from '../types/poll';

const client = new Client()
    .setEndpoint(import.meta.env.VITE_APPWRITE_ENDPOINT)
    .setProject(import.meta.env.VITE_APPWRITE_PROJECT_ID);

const databases = new Databases(client);

// Constants for Appwrite resources
const DATABASE_ID = import.meta.env.VITE_APPWRITE_DATABASE_ID;
const POLLS_COLLECTION_ID = import.meta.env.VITE_APPWRITE_POLLS_COLLECTION_ID;
const OPTIONS_COLLECTION_ID = import.meta.env.VITE_APPWRITE_OPTIONS_COLLECTION_ID;
const VOTES_COLLECTION_ID = import.meta.env.VITE_APPWRITE_VOTES_COLLECTION_ID;
const USERS_COLLECTION_ID = import.meta.env.VITE_APPWRITE_USERS_COLLECTION_ID;

// Modify the createValidId function
const createValidId = (prefix: string): string => {
    // Remove any non-alphanumeric characters and convert to lowercase
    const cleanPrefix = prefix.toLowerCase().replace(/[^a-z0-9]/g, '');
    
    // Generate a shorter random string
    const randomPart = Math.random().toString(36).substring(2, 8);
    
    // Combine with underscore (ensuring no leading underscore)
    const id = `${cleanPrefix}${randomPart}`;
    
    // Debug log
    console.log('Generated ID:', id);
    
    return id;
};

export const appwriteService = {
    async handleRequest<T>(request: Promise<T>): Promise<T> {
        try {
            return await request;
        } catch (error: any) {
            console.error('Appwrite request failed:', {
                message: error.message,
                code: error.code,
                type: error.type
            });
            throw error;
        }
    },

    async createPoll(poll: Poll): Promise<void> {
        const pollId = createValidId('poll');
        const doc = await this.handleRequest(databases.createDocument(
            DATABASE_ID,
            POLLS_COLLECTION_ID,
            pollId,
            {
                question: poll.question,
                created_at: new Date().toISOString(),
                votes_count: 0
            }
        ));

        // Create options
        await Promise.all(poll.options.map(option =>
            this.handleRequest(databases.createDocument(
                DATABASE_ID,
                OPTIONS_COLLECTION_ID,
                createValidId('opt'),
                {
                    poll_id: doc.$id,
                    text: option.text,
                    image_url: option.imageUrl,
                    votes: 0
                }
            ))
        ));
    },

    async getPolls(): Promise<Poll[]> {
        const response = await this.handleRequest(databases.listDocuments(
            DATABASE_ID,
            POLLS_COLLECTION_ID,
            [Query.orderDesc('created_at')]
        ));

        const polls = await Promise.all(response.documents.map(async (poll) => {
            const options = await this.handleRequest(databases.listDocuments(
                DATABASE_ID,
                OPTIONS_COLLECTION_ID,
                [Query.equal('poll_id', poll.$id)]
            ));

            return {
                id: poll.$id,
                question: poll.question,
                created_at: poll.created_at,
                votes_count: poll.votes_count,
                options: options.documents.map(opt => ({
                    id: opt.$id,
                    text: opt.text,
                    imageUrl: opt.image_url,
                    votes: opt.votes
                }))
            };
        }));

        return polls;
    },

    async getPoll(id: string): Promise<Poll | null> {
        try {
            const poll = await this.handleRequest(databases.getDocument(
                DATABASE_ID,
                POLLS_COLLECTION_ID,
                id
            ));

            const options = await this.handleRequest(databases.listDocuments(
                DATABASE_ID,
                OPTIONS_COLLECTION_ID,
                [Query.equal('poll_id', id)]
            ));

            return {
                id: poll.$id,
                question: poll.question,
                created_at: poll.created_at,
                votes_count: poll.votes_count,
                options: options.documents.map(opt => ({
                    id: opt.$id,
                    text: opt.text,
                    imageUrl: opt.image_url,
                    votes: opt.votes
                }))
            };
        } catch {
            return null;
        }
    },

    async votePoll(pollId: string, optionId: string, userId: string): Promise<boolean> {
        try {
            // Check if already voted
            const votes = await this.handleRequest(databases.listDocuments(
                DATABASE_ID,
                VOTES_COLLECTION_ID,
                [
                    Query.equal('poll_id', pollId),
                    Query.equal('user_id', userId)
                ]
            ));

            if (votes.documents.length > 0) {
                return false;
            }

            // Record vote
            await this.handleRequest(databases.createDocument(
                DATABASE_ID,
                VOTES_COLLECTION_ID,
                ID.unique(),
                {
                    poll_id: pollId,
                    option_id: optionId,
                    user_id: userId,
                    created_at: new Date().toISOString()
                }
            ));

            // Update option votes
            const option = await this.handleRequest(databases.getDocument(
                DATABASE_ID,
                OPTIONS_COLLECTION_ID,
                optionId
            ));

            await this.handleRequest(databases.updateDocument(
                DATABASE_ID,
                OPTIONS_COLLECTION_ID,
                optionId,
                { votes: option.votes + 1 }
            ));

            // Update poll total
            const poll = await this.handleRequest(databases.getDocument(
                DATABASE_ID,
                POLLS_COLLECTION_ID,
                pollId
            ));

            await this.handleRequest(databases.updateDocument(
                DATABASE_ID,
                POLLS_COLLECTION_ID,
                pollId,
                { votes_count: poll.votes_count + 1 }
            ));

            return true;
        } catch {
            return false;
        }
    },

    async deletePoll(id: string): Promise<boolean> {
        try {
            await this.handleRequest(databases.deleteDocument(
                DATABASE_ID,
                POLLS_COLLECTION_ID,
                id
            ));
            return true;
        } catch {
            return false;
        }
    },

    async getUserInfo(): Promise<{ id: string; username: string }> {
        // Get or create user ID
        let userId = localStorage.getItem('userId');
        console.log('Stored userId:', userId); // Debug log
        
        if (!userId || userId.length > 36 || /^_/.test(userId)) {
            userId = createValidId('user');
            console.log('New userId created:', userId); // Debug log
            localStorage.setItem('userId', userId);
        }

        try {
            const user = await this.handleRequest(databases.getDocument(
                DATABASE_ID,
                USERS_COLLECTION_ID,
                userId
            ));
            return {
                id: user.$id,
                username: user.username
            };
        } catch (error) {
            // Single attempt to create user
            try {
                const defaultUsername = `User${userId.slice(-4)}`;
                const newUser = await this.handleRequest(databases.createDocument(
                    DATABASE_ID,
                    USERS_COLLECTION_ID,
                    userId,
                    { username: defaultUsername }
                ));
                return {
                    id: newUser.$id,
                    username: newUser.username
                };
            } catch (createError) {
                console.error('Failed to create user:', createError);
                // Return default user info
                return {
                    id: userId,
                    username: `User${userId.slice(-4)}`
                };
            }
        }
    },

    async updateUsername(username: string): Promise<boolean> {
        const userId = localStorage.getItem('userId');
        console.log('UpdateUsername - userId:', userId); // Debug log
        
        if (!userId) return false;

        try {
            // Try to get the document first
            await this.handleRequest(databases.getDocument(
                DATABASE_ID,
                USERS_COLLECTION_ID,
                userId
            ));

            // If document exists, update it
            await this.handleRequest(databases.updateDocument(
                DATABASE_ID,
                USERS_COLLECTION_ID,
                userId,
                { username }
            ));
            return true;
        } catch (error) {
            // If document doesn't exist (404) or other error, create new document
            try {
                await this.handleRequest(databases.createDocument(
                    DATABASE_ID,
                    USERS_COLLECTION_ID,
                    userId,
                    { username }
                ));
                return true;
            } catch (createError) {
                console.error('Failed to create/update user document:', createError);
                return false;
            }
        }
    },

    async checkConnection(): Promise<boolean> {
        try {
            // Try to list a single document to verify connection
            await this.handleRequest(databases.listDocuments(
                DATABASE_ID,
                POLLS_COLLECTION_ID,
                [Query.limit(1)]
            ));
            return true;
        } catch (error) {
            console.error('Connection check failed:', error);
            return false;
        }
    }
}; 